<template>
  <div class="home">
    <p>{{ getUsers.length }} Users have registered</p>
    <Home/>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import Home from '@/components/Home.vue';

export default {
  name: 'home',
  components: {
    Home,
  },
  data() {
    return {
      users: [],
    };
  },
  computed: {
    ...mapActions([
      'fetchUsers',
    ]),
    ...mapGetters([
      'getUsers',
    ]),
  },
  created() {
    this.$store.dispatch('fetchUsers');
  },
};
</script>
